html,
body {
    width: 100%;
    height: 100%;
    overflow: hidden;
    margin: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    font-family: 'Open Sans Condensed', sans-serif;
}

div[class*=box] {
    height: 33.33%;
    width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
}

.box1 { background-color: #4354fa;
}

.box11{
    background-color: #e30ed7;
}

.btn {
    line-height: 50px;
    height: 50px;
    text-align: center;
    width: 250px;
    cursor: pointer;
}

.btnone {
    color: #FFF;
    transition: all 0.3s;
    position: relative;
}
.btnone span {
    transition: all 0.3s;
}
.btnone::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0;
    transition: all 0.3s;
    border-top-width: 1px;
    border-bottom-width: 1px;
    border-top-style: solid;
    border-bottom-style: solid;
    border-top-color: rgba(255,255,255,0.5);
    border-bottom-color: rgba(255,255,255,0.5);
    transform: scale(0.1, 1);
}
.btnone:hover span {
    letter-spacing: 2px;
}
.btnone:hover::before {
    opacity: 1;
    transform: scale(1, 1);
}
.btnone::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    transition: all 0.3s;
    background-color: rgba(255,255,255,0.1);
}
.btnone:hover::after {
    opacity: 0;
    transform: scale(0.1, 1);
}
